.name {
  text-align: center;
}
.website-link {
  width: 25%;
  border-radius: 7px;
  display: block;
  margin: 30px auto;
  text-align: center;
  border: 2px solid var(--font-color);
  transition: 0.25s;
  padding: 10px;
  font-weight: bold;
  color: #000;
  font-size: 16px;
  box-shadow: 0 0 10px 0 var(--font-color);
}
.website-link:hover {
  cursor: pointer;
  box-shadow: 0 0 15px 0 var(--font-color);
  transition: 0.25s;
}
h2 {
  color: var(--font-color);
}

@media screen and (max-width: 768px) {
  .website-link {
    width: 90%;
    padding: 0;
    padding: 10px;
    font-size: 16px;
  }
}
